import { Metadata } from "next";
// NOTE: Renamed the imported component to avoid conflict with the default export name
import NewsUpdatesClient from "@/components/NewsUpdates"; 
import Layout from "@/components/Layout";
import { client } from "@/lib/sanity";
import RightSidebar from "@/components/RightSidebar"; // <<< NEW IMPORT

// ----------------------
// Fetch Logic - Fetches ALL data needed for the page and sidebar
// ----------------------
async function getNewsPageData() {
    // 1. Main News list query
    const newsQuery = `
      *[_type == "newsUpdate"] | order(publishedAt desc) {
        _id, title, slug, type, publishedAt, featuredImage, readTime, downloadUrl, isNew
      }
    `;

    // 2. Recent Blogs (for sidebar - using blogPost type)
    const recentBlogsQuery = `
      *[_type == "blogPost"] | order(publishedAt desc)[0...3] {
        _id, title, slug, publishedAt, category->{name}
      }
    `;

    // 3. Recent News (for sidebar - using newsUpdate type)
    const recentNewsQuery = `
      *[_type == "newsUpdate"] | order(publishedAt desc)[0...4] {
        _id, title, slug, publishedAt, type
      }
    `;
    
    try {
      // Fetch all required data in parallel
      const [news, featuredBlogs, featuredNews] = await Promise.all([
        client.fetch(newsQuery, {}, { next: { revalidate: 60 } }),
        client.fetch(recentBlogsQuery, {}, { next: { revalidate: 3600 } }), // Longer cache for sidebar content
        client.fetch(recentNewsQuery, {}, { next: { revalidate: 3600 } }), // Longer cache for sidebar content
      ]);

      return { news, featuredBlogs, featuredNews };
    } catch (error) {
      console.error("Error fetching news page data:", error);
      // Return empty arrays on failure to prevent app crash
      return { news: [], featuredBlogs: [], featuredNews: [] }; 
    }
}

// ----------------------
// Metadata (Unchanged)
// ----------------------
export const metadata: Metadata = {
    title: "News & Updates - Legal Case Updates & Publications | Commercial Law Chamber",
    description: "Stay informed with latest news, case updates, publications, and deal corner updates from Commercial Law Chamber.",
    keywords: "legal news, case updates, legal publications, tax law news, commercial disputes updates",
    openGraph: {
        title: "News & Updates - Legal Case Updates & Publications",
        description: "Latest news, case updates, and publications on tax law, commercial disputes, and regulatory developments.",
        type: "website",
        url: "https://www.clclaw.in/news",
        images: [{ url: "https://www.clclaw.in/clc-logo.png" }],
    },
};

// ----------------------
// Server Component
// ----------------------
export default async function NewsPage() {
    const { news, featuredBlogs, featuredNews } = await getNewsPageData(); // <<< Call updated fetch function

    return (
        <Layout>
            <div className="flex flex-col lg:flex-row">
                {/* Main Content Area (Client Component) */}
                <div className="flex-1 min-w-0">
                    <NewsUpdatesClient initialNews={news} /> {/* NOTE: Using alias NewsUpdatesClient */}
                </div>

                {/* Sidebar Area (Server Component) */}
                <div className="lg:w-[350px] lg:flex-shrink-0">
                    <RightSidebar 
                        featuredBlogs={featuredBlogs} 
                        featuredNews={featuredNews} 
                    /> {/* <<< PASS DATA AS PROPS */}
                </div>
            </div>
        </Layout>
    );
}